.navbar {
  background: $bg-header-color;
  .navbar-brand {
    height: auto;
    margin-top: 23px;
  }
  .navbar-collapse {
    display: none;
  }
  .navbar-nav {
    li {
      font-family: $montserrat-regular;
      text-transform: uppercase;
      &:last-of-type {
        font-family: $montserrat-bold;
      }
      a {
        background: none;
        color: $secondary-color-light;
        padding: 15px 20px;
        &:hover,
        &:focus,
        &.active {
          background: none;
          color: $primary-color;
        }
      }
    }
  }
  .navbar-toggle {
    border-color: $quaternary-color;
    padding: 14px 10px;
    border-radius: 100%;
    position: absolute;
    margin: 26px 0;
    right: 15px;
    .icon-bar {
      background: $quaternary-color;
    }
  }
}

@media (min-width: $screen-sm-min) {
  .navbar {
    margin-bottom: 0;
    border-radius: 0;
    .navbar-collapse {
      display: block;
    }
    .navbar-nav {
      height: 96px;
      li {
        margin-top: 23px;
      }
      &.navbar-center {
        position: absolute;
        left: 50%;
        transform: translateX(-50%);
        li {
          position: relative;
          &:not(:last-child) {
            &:after {
              content: '';
              background: url('../img/spritesheet.png') no-repeat -56px -5px;
              position: absolute;
              width: 4px;
              height: 4px;
              display: block;
              top: 50%;
              transform: translateY(-50%);
              right: 0;
            }
          }
        }
      }
      &.navbar-right {
        li {
          &:last-of-type {
          border-left: 1px solid $quaternary-color;
          a {
            padding-right: 60px;
            padding-left: 35px;
            position: relative;
            &:after {
              content: '';
              display: block;
              position: absolute;
              width: 41px;
              height: 41px;
              top: 5px;
              right: 0;
              background: url('../img/spritesheet.png') no-repeat -5px -5px;
            }
          }
        }
        }
      }
    }
  }
}